.movies__poster {
  padding: 18px 0;
  width: 100%;
  height: 200px;
  object-fit: cover;
}

@media screen and (max-width: 1279px) {
  .movies__poster {
    height: 190px;
  }
}

@media screen and (max-width: 762px) {
  .movies__poster {
    height: 168px;
    padding: 14px 0;
  }
}
