.error__explanation {
  font-size: 16px;
  line-height: 19px;
  text-align: center;
  margin: 0;
  padding-top: 5px;
}

@media screen and (max-width: 700px) {
  .error__explanation {
    font-size: 12px;
    line-height: 15px;
  }
}

@media screen and (max-width: 400px) {
  .error__explanation {
    padding-top: 10px;
  }
}